<template>
  <div class="wyl_big">
    <!-- header -->
    <header>
      <!---->
      <h3></h3>
      <div class="right">
        <img
          src="data:image/png;base64,"
          alt
          class="search"
          @click="back"
        />
      </div>
    </header>

    <!--wyl_nav -->
    <van-dropdown-menu class="wyl_nav" active-color="#EE8032" ref="classreseter">
      <van-dropdown-item title="">
        <!--option one -->
        <div class="nkl_option1" v-if="attrclassify">
          <div v-for="(m,i) in attrclassify" :key="i">
            <p>{{ m.name }}</p>
            <div>
              <span
                v-for="(sm,si) in m.child"
                :key="si" 
                :class="{'active': m.name==='' ? 
                    classindex_grade===si : 
                    classindex_subject===si
                }"
                @click="decidetip(m,sm,si)"
              >{{ sm.name }}</span>
            </div>
          </div>

          <div>
            <button @click="resetclass"></button>
            <button
              @click="classifybygradeandsubject({
              page,
              limit,
              attr_val_id:class_id_grade+','+class_id_subject,
            })"
            ></button>
          </div>
        </div>
      </van-dropdown-item>

      <!-- option two -->
      <van-dropdown-item title="">
        <div class="nkl_option2">
          <p
            v-for="(item,index) in option2"
            :key="index"
            :class="{'active':sort_by==index}"
            @click="sortthem(index)"
          >{{item}}</p>
        </div>
      </van-dropdown-item>
      <!-- option three -->
      <van-dropdown-item title="">
        <div class="nkl_option3">
          <span
            v-for="(item,index) in option3"
            :key="index"
            :class="{'hidden':!item,'active':type_index==index}"
            @click="choosetype(index,(item==''))"
          >{{item}}</span>
        </div>
      </van-dropdown-item>
    </van-dropdown-menu>

    <!-- main -->

    <div class="list">
      <van-pull-refresh :immediate-check='false' v-model="refreshing" @refresh="onRefresh" class="context">
        <van-list
          v-model="pullloading"
          :finished="finished"
          finished-text=""
          @load="onLoad"
          class="item"
        >
          <van-cell class="van-clearfix" v-for="(item,index) in MainCourseList" :key="index">
            <template #default>
              <ldcell class="ii-item" :m="item" @click="todetail(item.id)"></ldcell>
            </template>
          </van-cell>
        </van-list>
      </van-pull-refresh>
    </div>
    <!--  -->

    <zzFooter></zzFooter>
    <!-- <wylIcon></wylIcon> -->
  </div>
</template>

<script>
import ldcell from "@/components/ldcell";
// 
import zzFooter from "../footbar";

export default {
  name: "",
  data() {
    return {
      // pull-refresh-list
      pulllist: [],
      pullloading: false,
      finished: false,
      refreshing: false,
      // filt data
      page: 1,
      limit:0,
      type_index: 0,
      course_type: 0,
      is_vip: 0,
      sort_by: 0,
      class_id_grade: "",
      class_id_subject: "",
      // style switch
      classindex_grade: "",
      classindex_subject: "",
      // data for tips
      attrclassify: null,
      option2: ["", "", "", "", ""],
      option3: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      loading: false,
      MainCourseList: [],
    };
  },
  props: {},
  components: {
    zzFooter,
    ldcell
  },
  filters: {
    //  
    dd(a) {
      if (a == 0) {
        return "";
      } else {
        return "" + Number(a).toFixed(2);
      }

      return a;
    }
  },
  computed: {
    transtime_term() {
      return (t1, t2) => {
        let tarr = null;
        tarr = this.getime(t1);
        let s1 = tarr[1] + "" + tarr[2] + " " + tarr[3] + ":" + tarr[4];
        tarr = this.getime(t2);
        let s2 = tarr[1] + "" + tarr[2] + " " + tarr[3] + ":" + tarr[4];
        return s1 + " ~ " + s2;
      };
    }
  },
  mounted() {
    this.classifybygradeandsubject({
        page: this.page,
        limit: this.limit,
    });
    this.classify();
  },
  methods: {
    onLoad() {
      setTimeout(() => {
        this.pullloading = false;

        if (this.limit >= 40) {
          this.finished = true;
        }
        this.limit+=5
        console.log('onload is running')
        this.classifybygradeandsubject().then(res=>{
          console.log(res)
        })
      }, 200);
    },
    onRefresh() {
      this.finished = false;
      this.pullloading = true;
      this.resetclass()
      this.refreshing = false;
    },
    getime(numstr) {
      let key = numstr + "000" - 0;
      let t = new Date(key),
        tarr = [
          t.getFullYear(),
          t.getMonth() + 1,
          t.getDate(),
          t.getHours(),
          t.getMinutes()
        ].map(function(m) {
          return ("" + m).padStart(2, "0");
        }),
        str = tarr[1] + "" + tarr[2] + " " + tarr[3] + ":" + tarr[4];
      return tarr;
    },
    //   
    choosetype(index, boo) {
      this.type_index = index;
      if (boo) {
        this.course_type = 0;
        this.is_vip = 1;
      } else {
        this.course_type = index;
        this.is_vip = 0;
      }
      this.classifybygradeandsubject({
        page: this.page,
        limit: this.limit,
        course_type: this.course_type,
        is_vip: this.is_vip
      });
    },
    sortthem(index) {
      this.sort_by = index;
      this.classifybygradeandsubject({
        page: this.page,
        limit: this.limit,
        order_by: this.sort_by
      });
    },
    decidetip(m, sm, si) {
      if (m.name === "") {
        this.classindex_grade = si;
        this.class_id_grade = sm.id;
      } else {
        this.classindex_subject = si;
        this.class_id_subject = sm.id;
      }
    },
    // 
    resetclass() {
      this.classindex_grade = "";
      this.classindex_subject = "";
      this.class_id_grade = "";
      this.class_id_subject = "";
      this.sort_by = 0;
      this.course_type = 0;
      this.limit=10;
      this.page=1;
      this.classifybygradeandsubject();
      this.$refs.classreseter.toggleItem();
    },
    async classifybygradeandsubject(
      paramobj = {
        page: this.page,
        limit: this.limit,
        // course_type is_vip 
        course_type: this.course_type,
        is_vip: this.is_vip,
        classify_id: "",
        // attr_val_id 
        attr_val_id: this.class_id_grade + "," + this.class_id_subject,
        // order_by 
        order_by: this.sort_by
      }
    ) {
      if(paramobj.attr_val_id){
        let paramarr=paramobj.attr_val_id.split(',')
        if(!(paramarr[0]&&paramarr[1])&&(paramarr[0]||paramarr[1])){
          paramobj.attr_val_id=paramarr[0]?paramarr[0]:paramarr[1]
        }
      }
      let {
        data: res
      } = await this.$http.get(`https://www.365msmk.com/api/app/courseBasis`, {
        params: paramobj
      });
      let { data } = res;
      // console.log(data)
      this.MainCourseList = data.list;
      this.$refs.classreseter.toggleItem();
      return {
        page: this.page,
        limit: this.limit,
        // course_type is_vip 
        course_type: this.course_type,
        is_vip: this.is_vip,
        classify_id: "",
        // attr_val_id 
        attr_val_id: this.class_id_grade + "," + this.class_id_subject,
        // order_by 
        order_by: this.sort_by
      }
    },
    todetail(bid) {
      // if(0){
      this.$router.push({ path: "/WylCD", query: { bid } });
      // }
    },
    back() {
      this.$router.push("/zjySearch");
    },
    //  
    async classify() {
      let {
        data: res
      } = await this.$http.get(
        `https://www.365msmk.com/api/app/courseClassify?`,
        { params: {} }
      );
      let { data } = res;
      this.attrclassify = data.attrclassify;
      console.log(this.attrclassify);
    }
  }
};
</script>

<stylescoped lang='scss'>
.wyl_big {
  background: #f0f2f5;
  height: 100%;
  font-size: 0.315rem;
  width: 100%;
  // header
  header {
    position: fixed;
    top: 0;
    z-index: 4;
    width: 100%;
    height: 0.9rem;
    background: #fff;
    h3 {
      font-size: 0.36rem;
      color: #595959;
      position: absolute;
      left: 50%;
      top: 25%;
      transform: translateX(-50%);
      font-weight: normal;
    }
    .right {
      position: absolute;
      right: 0.4rem;
      top: 25%;
      height: 100%;
      float: right;
      font-size: 0.4rem;
      color: #595959;

      .search {
        width: 0.45rem;
        height: 0.45rem;
      }
    }
  }
  .wyl_nav {
    width: 100%;
    height: 0.9rem;
    position: fixed;
    top: 0.89rem;
    background: #fff;
    line-height: 0.9rem;
    left: 0%;
    z-index: 6;
    overflow: hidden;

    // 1
    .nkl_option1 {
      min-height: 6.66rem;
      text-align: left;
      > div {
        margin-left: 0.28rem;
        padding: 0.3rem 0 0.34rem 0;
        border-bottom: 0.02rem solid #f7f7f7;
        box-sizing: border-box;
        min-height: 2.6rem;

        p {
          font-size: 0.3rem;
          color: #737373;
        }
        div {
          span {
            display: inline-block;
            width: 1.36rem;
            height: 0.66rem;
            background: #f5f5f5;
            color: #898989;
            font-size: 0.3rem;
            margin-right: 0.42rem;
            text-align: center;
            line-height: 0.66rem;
            border-radius: 0.06rem;
            margin-top: 0.16rem;
            &.active {
              color: #eb6100;
              background: #ebeefe;
            }
          }
        }
      }
      div:nth-of-type(3) {
        padding: 0.29rem 0 0.46rem 0;
        box-sizing: border-box;
        height: 1.46rem;
        line-height: 0.5rem;
        button:nth-of-type(1) {
          width: 3.34rem;
          height: 0.66rem;
          border: 0.02rem solid #d8d8d8;
          font-size: 0.3rem;
          background: #fff;
          border-radius: 0.08rem;
          margin-right: 0.19rem;
          color: #6f6f6f;
        }
        button:nth-of-type(2) {
          width: 3.34rem;
          height: 0.66rem;
          background: #eb6100;
          border-radius: 0.08rem;
          border: 0.02rem solid #eb6100;
          color: #fff;
          font-size: 0.3rem;
        }
      }
    }
    //2
    .nkl_option2 {
      height: 5.05rem;
      p {
        height: 0.98rem;
        font-size: 0.27rem;
        line-height: 0.98rem;
        color: #434343;
        text-align: center;
        border: 0.015rem solid #f9f9f9;
        &.active {
          color: #eb6100;
        }
      }
    }
    //3
    .nkl_option3 {
      padding: 0.2rem 0 0 0.3rem;
      box-sizing: border-box;
      height: 3.06rem;
      text-align: left;
      span {
        display: inline-block;
        width: 1.35rem;
        height: 0.66rem;
        background: #f5f5f5;
        color: #646464;
        font-size: 0.3rem;
        margin-right: 0.42rem;
        text-align: center;
        line-height: 0.66rem;
        border-radius: 0.06rem;
        margin-top: 0.14rem;
        &.hidden {
          display: none;
          visibility: hidden;
        }
        &.active {
          color: #eb6100;
          background: #ebeefe;
        }
      }
    }
  }
  // nav
  .nav {
    width: 100%;
    height: 0.9rem;
    position: fixed;
    top: 0.89rem;
    background: #fff;
    line-height: 0.9rem;
    left: 0%;
    z-index: 4;
    overflow: hidden;
    div {
      display: flex;
      align-items: center;
      justify-content: space-around;
      background: #fff;
      p {
        width: 33%;
        text-align: center;
        height: 0.5rem;
        line-height: 0.5rem;
      }
    }
  }

  // main

  .list {
    width: 100%;
    margin: 2rem 0 0;
    background: #f0f2f5;
    .context {
      width: 94%;
      margin: 1rem auto 0;
      .item {
        background: #fff;
        border-radius: 0.1333rem;
        padding: 0 0.37rem;
        margin-bottom: 0.4rem;
        margin-top: 0.2rem;

        .ii-item {
          position: relative;
          .ii-title {
            text-align: left;
            padding-top: 0.4rem;
            font {
              font-size: 0.32rem;
              color: #333;
            }
          }
          div.ii-time {
            width: 100%;
            display: flex;
            align-items: center;
            p {
              flex: 1;
              text-align: left;
              height: 0.8rem;
              line-height: 0.8rem;
              font-size: 0.28rem;
              font-weight: normal;
              color: #666;
            }
            p.after-icon {
              flex: 3;
            }
          }

          div.ii-teacher {
            height: 1.7rem;
            display: flex;
            div.teacher-item {
              display: flex;
              height: 100%;
              align-items: center;
              img {
                width: 0.72rem;
                height: 0.72rem;
                border-radius: 50%;
              }

              font {
                text-align: left;
                font-size: 0.28rem;
                color: rgba(0, 0, 0, 0.45);
                margin-left: 0.22rem;
                margin-right: 0.46rem;
              }
            }
          }
          p.ii-info {
            border-top: 0.01rem solid #f5f5f5;
            height: 1.17rem;
            line-height: 1.17rem;
            display: flex;
            font-size: 0.34rem;
            span.person {
              flex: 1;
              text-align: left;
              font-size: 0.28rem;
              color: rgba(0, 0, 0, 0.45);
            }
            font.free {
              text-align: right;
              padding-right: 0.2rem;
              flex: 1;
              font-size: 0.34rem;
              color: #44a667;
            }
            font.price {
              text-align: right;
              padding-right: 0.2rem;
              flex: 1;
              font-size: 0.34rem;
              color: orange;
            }
          }
          i {
            position: absolute;
            right: 0.4rem;
            top: 1.73333rem;
            background-size: 100% 100%;
            width: 1.57333rem;
            height: 1.28rem;
          }
        }
      }
    }
  }
}
</style>