<template>
  <div id="app">
    <div class="zjyheader">
      <div>
        <van-icon name="arrow-left" @click="onback" />
      </div>
      <div>
        <input class="zjyIn" type="text" placeholder=""  v-model="keywords"/>
      </div>
      <div @click="runsearch"></div>
    </div>
    <div class="zjyHis">
      <p></p>
      <p>
        <img src="http://t7.baidu.com/it/u=2566816517,3864068751&fm=193" alt />
      </p>
    </div>
    <div class="mainarea" v-if="seeklist">
      <ldcell 
        v-for='(m,i) in seeklist' :key='i'
        :m='m'
      ></ldcell>
    </div>
  </div>
</template>


<script>
import Vue from 'vue'
import ldcell from '@/components/ldcell'
Vue.component('ldcell',ldcell)
export default {
  data() {
    return {
      keywords:'',
      seeklist:null,
      records:[],
    };
  },
  created() {},
  mounted() {},
  methods: {
    onback(){
      this.$router.go(-1)
    },
    async runsearch(){
      let {data:res}=await this.$http.get(
        `https://www.365msmk.com/api/app/courseBasis`,{params:{
          limit:10,
          page:1,
          course_type:0,
          keywords:this.keywords,
        }}
      )
      let {data}=res
      console.log(data)
      this.seeklist=data.list
    }
  }
};
</script>


<style scoped lang="scss">
.zjyheader {
  width: 100%;
  height: 1.1rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-size: 0.3rem;
}


.zjyHis > p > img {
  width: 0.4rem;
  height: 0.35rem;
}
.zjyHis > p:nth-of-type(1){
  font-weight: bold;
}
.zjyIn {
  background: #e4e7ed;
  border: none;
  border-radius: 3rem;
  padding: 0.2rem 0.8rem;
}
.zjyHis {
  width: 90%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.3rem;
}
.zjyheader > div:nth-of-type(3) {
  color: #e0e0df;
}
</style>