
<style scoped lang='scss'>
.ii-item {
          position: relative;
          .ii-title {
            text-align: left;
            padding-top: 0.4rem;
            font {
              display: inline-block;
              font-size: 0.32rem;
              color: #333;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
              height: 0.4rem;
              width: 96%;
            }
          }
          div.ii-time {
            width: 100%;
            display: flex;
            align-items: center;
            p {
              flex: 1;
              text-align: left;
              height: 0.8rem;
              line-height: 0.8rem;
              font-size: 0.28rem;
              font-weight: normal;
              color: #666;
            }
            p.after-icon {
              flex: 3;
            }
          }

          div.ii-teacher {
            height: 1.7rem;
            display: flex;
            div.teacher-item {
              display: flex;
              height: 100%;
              align-items: center;
              img {
                width: 0.72rem;
                height: 0.72rem;
                border-radius: 50%;
              }

              font {
                text-align: left;
                font-size: 0.28rem;
                color: rgba(0, 0, 0, 0.45);
                margin-left: 0.22rem;
                margin-right: 0.46rem;
              }
            }
          }
          p.ii-info {
            border-top: 0.01rem solid #f5f5f5;
            height: 1.17rem;
            line-height: 1.17rem;
            display: flex;
            font-size: 0.34rem;
            span.person {
              flex: 1;
              text-align: left;
              font-size: 0.28rem;
              color: rgba(0, 0, 0, 0.45);
            }
            font.free {
              text-align: right;
              padding-right: 0.2rem;
              flex: 1;
              font-size: 0.34rem;
              color: #44a667;
            }
             font.price {
              text-align: right;
              padding-right: 0.2rem;
              flex: 1;
              font-size: 0.34rem;
              color: orange;
            }
          }
          i {
            position: absolute;
            right: 0.4rem;
            top: 1.73333rem;
            background-size: 100% 100%;
            width: 1.57333rem;
            height: 1.28rem;
          }
        }
</style>


<template>
  <div class="ii-item">
    <p class="ii-title">
      <font>{{ m.title }}</font>
    </p>
    <div class="ii-time">
    <p class="after-icon">
    {{ transtime_term(m.start_play_date,m.end_play_date) }}
    </p>
      <p>{{ m.total_periods }}</p>
    </div>
    <div class="ii-teacher">
      <div class="teacher-item" v-for="(sm,si) in m.teachers_list" :key='si'>
        <img
          class="zjy_img"
          :src="sm.teacher_avatar"
          alt
        />
        <font>{{ sm.teacher_name }}</font>
      </div>
    </div>
    <p class="ii-info">
      <span class="person">{{ m.sales_num }}</span>
    <font :class="m.price?'price':'free'" >
    <img v-if="m.price" src="@/assets/pic/gold.png" alt="">
    {{m.price?((m.price/100).toFixed(2)):''}}</font>
    </p>
  </div>
</template>


<script>
export default {
  data() {
    return {};
  },
  created() {},
  mounted() {},
  props:['m'],//[]
  methods: {
       getime(numstr){
      let key=numstr+'000'-0
        let t=new Date(key),
            tarr=[
                t.getFullYear(),
                t.getMonth()+1,
                t.getDate(),
                t.getHours(),
                t.getMinutes()
            ].map(function(m){
                return (''+m).padStart(2,'0')
            }),
            str=tarr[1]+''+tarr[2]+' '+tarr[3]+':'+tarr[4]
        return tarr
    },
  },
  computed: {
          transtime_term(){
      return (t1,t2)=>{
        let tarr=null;
        tarr=this.getime(t1)
        let s1=tarr[1]+''+tarr[2]+' '+tarr[3]+':'+tarr[4]
        tarr=this.getime(t2)
        let s2=tarr[1]+''+tarr[2]+' '+tarr[3]+':'+tarr[4]
        return s1+' ~ '+s2
      }
    }
  },
  watch: {},
  filters: {},
  directives: {},
  components: {}
};
</script>