<template>
  <div>
    <div class="header">
      <img src="../assets/pic/login-log.png" alt />
    </div>
    <div class="login">
      <div  class="p">
        <input type="text" v-model="tel" placeholder="" />
        <div></div>
        <!-- <hr /> -->
      </div>
      <div class="p">
        <input type="password" v-model="psw" placeholder="" />
        <div></div>
        <!-- <hr /> -->
      </div>
      
    </div>
    <p>
      <span></span>
      <span @click="toSms_login">/</span>
    </p>
    <div class="box">
        <div class="login-txt" @click="loginO"></div>
      </div>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  name: "",
  data() {
    return {
      tel: "",
      psw: "",
    };
  },
  props: {},
  created() {},
  mounted() {},
  methods: {
    toSms_login() {
        this.$router.push('/sms-login')
    },
    loginO() {
        console.log(123)
        this.$http.post('https://wap.365msmk.com/api/app/login',{mobile:this.tel,password:this.psw,type:1}).then(res=>{
            console.log(res)
            window.localStorage.setItem('userID',res.data.data.id)
            window.localStorage.setItem(
              "userToken",
              res.data.data.remember_token
            );
            this.$router.push('/Sou')
        })
    }
  },
  computed: {},
  watch: {},
  filters: {},
  directives: {},
  components: {}
};
</script>

<stylescoped lang="scss">
.header {
  width: 90%;
  margin: 30px auto;
  img {
    width: 100%;
  }
}
.login {
  width: 100%;
  .p {
    height: 50px;
    text-align: left;
    padding: 0 30px;
    input {
    width: 60%;
    height: 90%;
    font-size: .3rem;
    border: none;
    outline: none;
      &+div {
        border-bottom: 1px solid #ddd;
      }
      &:focus+div {
        border-bottom: 1px solid #eb6100;
      }
    }
  }
}
.box {
  width: 100%;
  .login-txt {
    margin: 20px auto;
    background: rgb(245, 76, 33);
    color: #fff;
    border-radius: 15px;
    text-align: center;
    width: 80%;
    height: 50px;
    line-height: 50px;
    font-size: 0.3rem;
  }
}
p {
