<template>
  <div class="wyl_big1">
    <div class="_before" ref='fake'></div>
    <div class="wyl_big1-header wyl_header">
      <van-nav-bar
        class="wyl_icon"
        left-arrow
        @click-left="onClickLeft"
        @click-right="onClickRight"
      >
        <template #title>
          <div class="title">
            <ul v-if="changehead" class="title-true">
              <li class="title-true-ld"
                @click="scrtowards('lesson_introduce')"
              ></li>
              <li class="title-true-ls"
                @click="scrtowards('lesson_schedule')"
              ></li>
              <li class="title-true-lc"
                @click="scrtowards('lesson_comment')"
              ></li>
            </ul>
            <div v-else class="title-false"></div>
          </div>
        </template>
        <template #right>
          <van-icon name="qr-invalid" size="18" />
        </template>
      </van-nav-bar>

      <van-overlay :show="show" @click="show=false">
        <div class="wrapper">
          <div class="block" @click.stop>
            <img src="@/assets/skm.jpg" alt />
          </div>
        </div>
      </van-overlay>
    </div>
    <div class="cd-body">
      <!-- main1 -->
      <div class="cd-info" v-if="info">
        <p class="info-title">{{ info.title }}</p>
        <i @click="coll()" :class="{'info-collect':1,'active':info.is_collect}"></i>
        <p class="info-price cd-free">{{ !info.is_free?((info.price/100).toFixed(2)):'' }}</p>
        <p class="info-price">
          <img
            src="https://msmk2019.oss-cn-shanghai.aliyuncs.com/uploads/image/20191HHDExgz0u1567065946.png"
            class="price-ico"
          />
          <font>1.00</font>
        </p>
        <div>
          <p class="info-com">{{ info.total_periods }} | {{ info.sales_num }}</p>
          <p class="info-com">
            
            {{ info.start_play_date | nowtime(info.start_play_year) }}
            -
            {{ info.end_play_date | nowtime(info.start_play_year) }}
          </p>
        </div>
      </div>
      <!-- main2 -->
      <div class="cd-teachers com-style">
        <p class="cd-title"></p>
        <ul>
          <li>
            <img src="https://baijiayun-wangxiao.oss-cn-beijing.aliyuncs.com/uploads/avatar.jpg" />
            <span></span>
          </li>
        </ul>
      </div>
      <!-- main3-->
      <div id="tro" class="cd-tro com-style" ref="lesson_introduce">
        <p class="cd-title"></p>
        <div class="cd-details">
          <p>yx^2</p>
        </div>
      </div>

      <!-- main4 -->
      <div id="list" class="cd-list com-style" ref="lesson_schedule">
        <p class="cd-title"></p>
        <div class="charpter">
          <div>
            <section class="period">
              <div class="charp-title">
                <span class="huifang"></span>
                
              </div>
              <p class="charp-time">
                
                <span>0308 10:30 - 12:30</span>
              </p>
            </section>
            <section class="period">
              <div class="charp-title">
                <span class="huifang"></span>
                er
              </div>
              <p class="charp-time">
                
                <span>0308 10:30 - 12:30</span>
              </p>
            </section>
            <section class="period">
              <div class="charp-title"> yx^2</div>
              <p data-v-8247b44a class="charp-time"></p>
            </section>
          </div>
        </div>
        <!---->
      </div>

      <!-- main5 -->
      <div id="com" class="cd-comment com-style" ref="lesson_comment">
        <p class="cd-title"></p>

        <div class="com-item-page">
          <!-- one  list -->
          <div role="feed" class="van-list">
            <ul>
              <li>
                <div class="evaluate-top">
                  <img
                    src="https://msmk2019.oss-cn-shanghai.aliyuncs.com/uploads/image/20206kRSVtmcMX1589444592.jpg"
                  />
                  <div class="evaluate-t-left">
                    <div class="info">
                      <span>15910732068</span>
                      <van-rate
                        class="van-rate"
                        v-model="value"
                        :size="15"
                        color="#ffd21e"
                        void-icon="star"
                        void-color="#eee"
                        :gutter="0.2"
                      />

                      <span class="times">2020-04-23 14:56</span>
                    </div>
                    <!---->
                    <div class="evaluate-btom">very go o g</div>
                  </div>
                </div>
              </li>
              <li>
                <div class="evaluate-top">
                  <img
                    src="https://msmk2019.oss-cn-shanghai.aliyuncs.com/uploads/image/20206kRSVtmcMX1589444592.jpg"
                  />
                  <div class="evaluate-t-left">
                    <div class="info">
                      <span>15910732068</span>
                      <van-rate
                        class="van-rate"
                        v-model="value"
                        :size="15"
                        color="#ffd21e"
                        void-icon="star"
                        void-color="#eee"
                        :gutter="0.2"
                      />

                      <span class="times">2020-04-23 14:56</span>
                    </div>
                    <!---->
                    <div class="evaluate-btom">very go o g</div>
                  </div>
                </div>
              </li>
            </ul>
            <div class="van-list__placeholder"></div>
          </div>

          <!-- two empty -->
          <div class="empty">
            <img src="../../assets/record.png" />
            <p data-v-0fa74510></p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      show: false,
      value: 0,
      info: null,
      teachers: null,
      bid: "",
      changehead: false
    };
  },
  // props: ['bid'],
  components: {},
  mounted() {
    window.addEventListener("scroll", this.vscroll, false);
    let bid = this.$route.query.bid;
    this.bid = bid;
    console.log(this.bid);
    this.initialdata(this.bid);
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    onClickRight() {
      // this.$router.push("/zjySearch");
      this.show = true;
    },
    getElementToPageTop(el) {
      if (el.parentElement) {
        return this.getElementToPageTop(el.parentElement) + el.offsetTop;
      }
      return el.offsetTop;
    },
    scrtowards(str){
      let docel = document.documentElement;
      let trs=this.$refs
      let gt_fk=trs.fake.clientHeight
      docel.scrollTop=this.getElementToPageTop(trs[str])-gt_fk;
    },
    vscroll() {
      let docel = document.documentElement;
      let trs=this.$refs
      let ld = trs.lesson_introduce;
      let gt_ld = this.getElementToPageTop(ld);
      let gt_fk=trs.fake.clientHeight
      if (docel.scrollTop >= (gt_ld-gt_fk)) {
        this.changehead = true;
      }else {
        this.changehead = false;
      }
      // console.log(docel.scrollTop)
    },
    async initialdata(bid) {
      console.log(bid);
      let { data: res } = await this.$http.get(
        `https://www.365msmk.com/api/app/courseInfo/basis_id=${bid}`
      );
      let { data } = res;
      console.log(res);
      this.info = data.info;
      this.teachers = data.teachers;
      console.log(this.info, this.teachers);
    },
    coll() {
      this.$http
        .post(`https://www.365msmk.com/api/app/collect`, {
          course_basis_id: this.info.collect_id,
          type: 1
        })
        .then(res => {
          console.log(res);
        });
    }
  },
  filters: {
    nowtime(t, y) {
      let time = new Date(t),
        tarr = [
          time.getFullYear(),
          time.getMonth() + 1,
          time.getDate(),
          time.getHours(),
          time.getMinutes(),
          time.getSeconds()
        ].map(function(m) {
          return ("" + m).padStart(2, "0");
        }),
        str = y + "." + tarr[1] + "." + tarr[2] + " " + tarr[3] + ":" + tarr[4];
      return str;
    },
    chaptime(start = "2020-02-18 13:00", end = "2020-02-18 15:00") {
      let ts = new Date(start),
        te = new Date(end),
        tarr1 = [
          ts.getFullYear(),
          ts.getMonth() + 1,
          ts.getDate(),
          ts.getHours(),
          ts.getMinutes()
        ].map(function(m) {
          return ("" + m).padStart(2, "0");
        }),
        tarr2 = [
          te.getFullYear(),
          te.getMonth() + 1,
          te.getDate(),
          te.getHours(),
          te.getMinutes()
        ].map(function(m) {
          return ("" + m).padStart(2, "0");
        }),
        str =
          tarr1[1] +
          "" +
          tarr1[2] +
          " " +
          tarr1[3] +
          ":" +
          tarr1[4] +
          " - " +
          tarr2[3] +
          ":" +
          tarr2[4];

      console.log(str);
      // return tarr
    }
  }
};
</script>

<stylescoped lang='scss'>
.wyl_big1 {
  ._before {
    
    height: 1.17rem;
  }
  &-header {
    // background: red; 
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 4;

    height: 1.17rem;
    line-height: 1.17rem;
    border-bottom: 0.02rem solid #f0f2f5;
    .wyl_icon {
      width: 100%;
      height: 1.17rem;
      box-sizing: border-box;
      .title {
        &-true {
          display: flex;
          justify-content: space-around;
          align-items: center;
          &>li {
            padding: 0 0.1rem;
          }
        }
      }
    }
    .wrapper {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
    }

    .block {
      width: 1.3rem;
      height: 1.3rem;
      background-color: #fff;
      img {
        width: 1.3rem;
        height: 1.3rem;
      }
    }
  }
  background: red;
  min-height: 5rem;
  box-sizing: border-box;
  // main
  .cd-body {
    text-align: left;
    background: #f0f2f5;
    padding-bottom: 0.4rem;
    box-sizing: border-box;

    // main1
    .cd-info {
      // width: 100%;
      padding: 0.25rem;
      background: #fff;
      position: relative;

      .info-title {
        font-size: 0.38rem;
        // font-weight: bold;
        color: #333;
        padding-right: 1.06rme;
        line-height: 0.613rem;
      }

      .info-collect {
        position: absolute;
        right: 0.26667rem;
        top: 0.13333rem;
        width: 0.66667rem;
        height: 0.66667rem;
        background: url("../../assets/notclcted.png") no-repeat 50%;
        background-size: 0.48rem 0.48rem;
        &.active {
          background: url("../../assets/clcted.png") no-repeat 50%;
          background-size: 0.48rem 0.48rem;
        }
      }

      .info-price {
        color: #eb6100;
        font-size: 0.4rem;
        img {
          width: 0.24rem;
          height: 0.24rem;
        }
        font {
          font-size: 0.45333rem;
          font-weight: 500;
          color: #ee1f1f;
          line-height: 0.85333rem;
        }
      }

      > div {
        .info-com {
          font-size: 0.32rem;
          font-weight: 400;
          color: rgba(0, 0, 0, 0.45);
          line-height: 0.8rem;
        }
      }
    }

    // main2
    .com-style {
      margin-top: 0.25rem;
      background: #fff;
      padding: 0.13rem 0.26rem;

      .cd-title {
        font-size: 0.32rem;
        font-weight: 500;
        color: #262626;
        padding-top: 0.2rem;
      }
      > ul {
        display: flex;
        flex-wrap: wrap;
        padding: 0.28rem 0;
        li {
          width: 20%;
          height: 1.6rem;
          display: flex;
          align-items: center;
          flex-direction: column;
          box-sizing: border-box;
          img {
            width: 0.8rem;
            height: 0.8rem;
            border-radius: 50%;
          }
          > span {
            padding: 0.21rem 0 0 0.4rem;
            font-size: 0.28rem;
            color: #595959;
          }
        }
      }
    }

    // main3
    .cd-tro {
      margin-top: 0.25rem;
      background: #fff;
      padding: 0.13rem 0.26rem;

      .cd-details {
        font-size: 0.36rem;
        padding: 0.4rem 0.13rem;
        p {
          width: 100%;
          font-size: 0.28rem;
        }
      }
    }

    // main4
    .cd-list {
      .charpter {
        padding: 0.2rem 0.26rem;
        section.period {
          padding: 0.1rem 0 0 0;
          .charp-title {
            position: relative;
            padding-left: 0.4rem;
            line-height: 0.8rem;
            font-size: 0.26rem;
            font-weight: 400;
            color: #595959;
            span.huifang {
              background: #ea7a2f;
              display: inline-block;
              width: 0.8rem;
              margin-right: 0.1rem;
              padding: 0 0.13333rem;
              height: 0.38rem;
              border-radius: 0.05333rem;
              font-size: 0.26rem;
              font-weight: 400;
              color: hsla(0, 0%, 100%, 0.85);
              line-height: 0.38rem;
              text-align: center;
            }
          }

          .charp-title:after {
            content: "";
            position: absolute;
            left: 0.05333rem;
            left: 0.53333vw;
            top: 50%;
            transform: translateY(-50%);
            width: 0.10667rem;
            width: 1.06667vw;
            height: 0.10667rem;
            height: 1.06667vw;
            border-radius: 50%;
            background: #eb6100;
          }

          .charp-time {
            font-size: 0.26rem;
            color: #8c8c8c;
            padding-left: 0.13rem;
            padding-bottom: 0.2rem;
            > span {
              padding-left: 0.2rem;
            }
          }
        }
      }
    }

    // main5
    .cd-comment {
      .com-item-page {
        //   one  list
        .van-list {
          // one1
          ul {
            list-style: none;

            li {
              padding: 0.2rem 0.2rem;
              .evaluate-top:nth-last-of-type(1) {
                border: 0;
              }
              .evaluate-top {
                display: flex;
                > img {
                  width: 0.6rem;
                  height: 0.6rem;
                  border-radius: 50%;
                }
                .evaluate-t-left {
                  flex: 1;

                  .info {
                    display: flex;
                    padding-left: 0.26667rem;
                    align-items: center;
                    span:nth-of-type(1) {
                      flex: 1.5;
                      font-size: 0.28rem;
                      font-weight: 400;
                      color: #333;
                      margin-right: 0.1rem;
                    }
                    .vant-rate {
                      flex: 3;
                      height: 0.4rem;
                      box-sizing: border-box;
                      user-select: none;
                    }

                    span.times {
                      //   flex: 2;
                      display: inline-block;
                      font-size: 0.2rem;
                      color: #999;
                      text-align: right;
                    }
                  }
                }
                .evaluate-btom {
                  margin-top: 0.2rem;
                  line-height: 0.4rem;
                  padding-left: 0.27rem;
                  color: #999;
                  font-size: 0.26rem;
                }
              }
            }
          }
          // one2
          .van-list__placeholder {
            height: 0;
            pointer-events: none;
          }
        }

        // two empty
        .empty {
          flex-direction: column;
          display: flex;
          justify-content: center;
          align-items: center;
          img {
            width: 4rem;
            height: 4rem;
          }
          > p {
            font-size: 0.3rem;
            color: #8c8c8c;
            text-align: center;
          }
        }
      }
    }
  }
}
</style>
